$color-base: #07060B;

@mixin main-font {
  font: 400 16px 'Open Sans', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  @include main-font;
  line-height: 1.5rem;
  color: $color-base;
}

a {
  color: $color-base;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  width: 90%;
  max-width: 600px;
  margin: 0 auto;
}

.result-status {
  flex: 1;
  padding-top: 2rem;
  cursor: pointer;

  &.result-status--loading {
    cursor: initial;
  }

  &> * {
    margin-bottom: 1rem;
  }
}

.intro {
  font-size: 1.1rem;
  line-height: 1.75rem;
  padding: 2rem 0;
  background: #f7f7f7;
}

.file-picker {
  visibility: hidden;
}

.image-container {
  position: relative;
}

.image-container__canvas {
  position: absolute;
  top: 0;
  left: 0;
}

table {
  font-size: .9rem;
  border-collapse: collapse;

  td, th {
    text-align: left;
    padding: 0.5rem .75rem;
  }
}
